.lube-recommender-block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  @include shadow-2;
  cursor: pointer;
  font-size: 18px;
}
.lube-recommender-wrapper.lube-recommender-overview .content-container {
  display: block;
}
.lube-recommender-wrapper.lube-recommender-overview .content-container .col-3 {
  height: 80%;
}
.lube-recommender-wrapper.lube-recommender-overview .lube-recommender-block {
  padding: 4vw 0 3.75vw;
}
.lube-recommender-wrapper.lube-recommender-overview .lube-recommender-block h5 {
  text-transform: uppercase;
  margin-top: 30px;
  font-size: 14px;
}
.lube-recommender-wrapper.lube-recommender-overview .lube-recommender-block .image-block {
  min-height: 92px;
}
.lube-recommender-tool-wrapper {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  min-height: calc(38vh);
  padding: 3.75vw;
  z-index: 2;
  box-sizing: border-box;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
}
.lube-recommender-tool-wrapper .progress {
  color: petronasGreen(base);
  font-size: 12px;
  font-weight: 900;
}
.lube-recommender-tool-wrapper .progress-bar {
  width: 120px;
  display: inline-block;
  margin: 0 10px 2px;
  height: 3px;
}
.lube-recommender-tool-wrapper h1 {
  color: black;
  font-weight: 700;
  font-size: 24px;
}
.lube-recommender-tool-wrapper .top-heading h1 {
  font-weight: 800;
  font-size: 26px;
}
.lube-recommender-tool-wrapper .top-heading h4 {
  color: petronasGreen(base);
  margin-bottom: 20px;
  font-size: 16px;
}
.lube-recommender-tool-wrapper .flex-wrapper {
  display: flex;
}
.lube-recommender-tool-wrapper .white-bg {
  position: relative;
  display: block;
  background-color: $white;
  @include shadow-1;
  border-radius: 2px;
  min-height: 100px;
  width: 100%;
  transition: all .3s ease;
  box-sizing: border-box;
  padding: 3.75vw;
  height: auto;
  min-height: calc(30.5vh);
}
.lube-recommender-tool-wrapper .back {
  position: relative;
  display: block;
  margin-bottom: 5px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
}
.lube-recommender-tool-wrapper .back .back-arrow {
  transform: rotate(180deg);
  font-size: .9em;
  top: -1px;
  margin-right: 3px;
}
.lube-recommender-tool-wrapper .back .forward-arrow {
  font-size: .9em;
  top: -1px;
  margin-left: 3px;
}
.lube-recommender-tool-wrapper .text-center {
  text-align: center;
}
.lube-recommender-tool-wrapper .text-right {
  text-align: right;
}
.lube-recommender-tool-wrapper .lube-recommender-block .inner {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-align-content: center;
  -ms-flex-line-pack: center;
  align-content: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  height: 15vw;
  padding: 1vw;
  font-weight: 300;
}
.lube-recommender-tool-wrapper .lube-recommender-block .inner img {
  padding-bottom: 20px;
}
.lube-recommender-tool-wrapper .lube-recommender-block .index {
  border-radius: 50%;
  background-color: white;
  color: petronasGreen(base);
  border: 4px solid petronasGreen(light2);
  width: 22px;
  height: 22px;
  padding: 10px;
  font-weight: 800;
  margin: -20px auto -10px auto;
}
.lube-recommender-tool-wrapper .lube-recommender-block .index.selected {
  background-color: petronasGreen(light1);
  color: white;
}
.lube-recommender-tool-wrapper .temp-block {
  @include shadow-1;
}
.lube-recommender-tool-wrapper .temp-block ul li {
  padding: 0.8vw 1.5vw;
  font-size: .85em;
}
.lube-recommender-tool-wrapper .temp-block ul li:hover {
  background-color: #eee;
  cursor: pointer;
}
.lube-recommender-tool-wrapper .top-wrapper .col {
  width: 20%;
}
.lube-recommender-tool-wrapper .top-wrapper .col.col-main {
  width: 60%;
}
.temp-tool hr {
  margin: 5px 0 0;
}
.temp-tool .temp-head {
  padding: 20px 0 10px;
  color: petronasGrey(base);
  border-bottom: 1px solid petronasGrey(light5);
}
.temp-tool .temp-head.temp-type {
  margin-bottom: 20px;
}
.temp-tool .temp-head.dropdown {
  background: url(/public/assets/images/icons/dropdown.svg) calc(90%) center no-repeat;
  padding-right: 25px;
  appearance: none;
  cursor: pointer;
}
.flex-top-container {
  display: flex;
  flex-direction: column;
  height: 90%;
  justify-content: center;
  padding-bottom: 50px;
}
.flex-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: stretch;
  align-items: center;
}
.flex-container.small-padding {
  padding-top: 20px;
}
